* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

img {
  width: 30px;
  margin-right: 5px;
}

ul {
  padding-left: 10px;
}

li {
  display: flex;
  align-items: center;
  list-style: none;
  margin-bottom: 5px;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
}
